import React from "react";
import { Row, Col, Badge } from "reactstrap";
import { Link } from "react-router-dom";
import cons from "../../../constants";
import { getUserFullName, getStatusInfo, fixNumber } from "helpers/UtilsHelper";
import avatar from "../../../assets/img/theme/default-avatar.png";
import "./Client.scss";
import moment from "moment";

export default function Client(props) {
  const { item, filterValue } = props;

  const status = getStatusInfo(cons.CLIENT_STATUS, item.status);
  return (
    <tr>
      {/* <td className="table-actions text-center table__width-100">
                <img
                    alt="..."
                    className="avatar rounded-circle"
                    src={item.avatar || avatar}
                />
            </td> */}
      <td className="table-user">
        <Link
          to={{ pathname: `/provider/clients/${item.id}`, query: filterValue }}
          className="btn-action__link"
        >
          <b>{getUserFullName(item)}</b>
        </Link>
      </td>
      <td className="table-actions text-center table__width-100">
        <Badge color={status.color} pill className="cursor-pointer">
          <b>{status.status || "Unknown"}</b>
        </Badge>
      </td>
      <td className="table-user">
        {/* <Link to={`/provider/clients/${item.id}`} className="btn-action__link">
          <b>{getUserFullName(item)}</b>
        </Link> */}

        {/* here show start date data */}
        {moment.utc(item.firstSeenAt).format("MM/DD/YYYY")}
      </td>

      <td className="table-actions text-center">
        <Col className="mr-2">
          <Row className="my-2 ">
            <b>{item.phoneNumber || "0000-1111-2222"}</b>
          </Row>
          <Row>
            <a
              href={`mailto:${item.email}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              {item.email}
            </a>
          </Row>
        </Col>
      </td>
      <td className="table-actions text-center">
        <Link
          to={{
            pathname: `/provider/clients/${item.id}`,
            state: {
              goToBillingPage: true,
            },
          }}
        >
          <b className="text-muted">
            Balance due: ${fixNumber(item.unpaidBalance)}
          </b>
        </Link>
      </td>
    </tr>
  );
}
