import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Button,
  Card,
  Table,
  Row,
  Col,
  CardHeader,
  Input,
  Form,
  Label,
} from "reactstrap";
import _ from "lodash";
import { Link } from "react-router-dom";
import { getList } from "../../../redux/actions/clientAction";
import PaginationBar from "../../../components/PaginationBar";
import cons from "../../../constants";
import Loading from "../../../components/Loading";
import Client from "./Client";
import "./ListClientAccount.scss";

class ListClientAccount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      clientData: {},
      clientArr: [],
      dropdownOpen: false,
      search: "",
      isLoading: true,
    };
    this.filters = {
      limit: 10,
      offset: 0,
      selected: 0,
      search: "",
      ordering: "first_name",
      desc: true,
      status: cons.CLIENT_STATUS[0].id,
    };
    this.searchRef = React.createRef();
    this.delaySearch = _.debounce(this.search, 300);
  }

  componentDidMount() {
    if (
      this.props.location.state === undefined ||
      this.props.location.state === null
    ) {
      this.props.getList(this.filters);
    } else {
      this.props.getList(this.props.location.state.filter);
    }
  }

  filter = (e) => {
    const { value } = e.target;
    this.filters.status = value;
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  sort = (e) => {
    let name = e.target.attributes.name.value;

    if (name && name === this.filters.ordering) {
      this.filters.desc = !this.filters.desc;
    } else {
      this.filters.ordering = name;
      this.filters.desc = true;
    }
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  onSearch = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({ [name]: value });
    this.delaySearch();
  };

  search = () => {
    this.filters.search = this.state.search.trim();
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.filters.limit = value;
      this.props.getList(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.props.getList(this.filters);
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  toggleAction = () => {
    this.setState((prevState) => ({
      dropdownOpen: !prevState.dropdownOpen,
    }));
  };

  render() {
    const { search } = this.state;
    const { clientData, loading } = this.props;

    const clientArr = clientData.result;

    const { limit, selected } = this.filters;
    const totalPage = Math.ceil(clientData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < clientData.count
          ? (selected + 1) * limit
          : clientData.count,
      total: clientData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };

    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };
    const sortClass = {
      title: this.filters.ordering,
      value: this.filters.desc,
      get: function (name) {
        if (this.title === name) {
          return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
        }
        return "sort";
      },
    };
    return (
      <>
        <Col>
          <Card className="mt-3">
            {loading && this.props.location.state === undefined && <Loading />}
            <CardHeader className="bg-transparent border-0">
              <Row className="d-flex">
                <Col md="3" lg="3" sm="6" className="mb-3">
                  <Row className="align-items-center justify-content-start">
                    {/* <div className="d-none d-sm-inline-block">
                      <b className="m-md-3 ml-3 ml-md-0">Filter</b>
                    </div> */}
                    <Col
                      className="header__override-col mt-3 mt-sm-0"
                      lg="9"
                      md="9"
                      sm="12"
                    >
                      <Input
                        type="select"
                        onChange={this.filter}
                        name="status"
                        value={this.filters.status}
                      >
                        {cons.CLIENT_STATUS.map((item, i) => {
                          return (
                            <option key={i} value={item.id}>
                              {item.status}
                            </option>
                          );
                        })}
                      </Input>
                    </Col>
                  </Row>
                </Col>

                <Col md="3" lg="3" sm="6" className="mb-3">
                  <Row className="align-items-center justify-content-start">
                    {/* <div className="d-none d-sm-inline-block">
                      <b className="m-md-3 ml-3 ml-md-0">Filter</b>
                    </div> */}
                    <Col
                      className="header__override-col mt-3 mt-sm-0"
                      lg="12"
                      md="12"
                      sm="12"
                    >
                      <Link to={`/provider/clients/create`}>
                        <Button color="success">Add Client</Button>
                      </Link>
                    </Col>
                  </Row>
                </Col>

                <Col md="6" lg="6" sm="6" className="mb-3">
                  <Row className="align-items-center justify-content-start">
                    <Col className="header__override-col">
                      <Form onSubmit={this.onSearch}>
                        <Input
                          type="text"
                          name="search"
                          value={search}
                          placeholder="Search..."
                          ref={(ref) => (this.searchRef = ref)}
                          onChange={this.onSearch}
                        />
                      </Form>
                    </Col>
                  </Row>
                </Col>
              </Row>
            </CardHeader>
            <Table className="align-items-center table-sort" responsive>
              <thead className="thead-light">
                <tr>
                  <th
                    className={sortClass.get("first_name")}
                    name="first_name"
                    onClick={this.sort}
                  >
                    Name
                  </th>

                  <th
                    className={`text-center ${sortClass.get("status")}`}
                    name="status"
                    onClick={this.sort}
                  >
                    Status
                  </th>
                  <th>Start Date</th>
                  <th> Info </th>
                  <th
                    className={`text-center ${sortClass.get("unpaid_balance")}`}
                    name="unpaid_balance"
                    onClick={this.sort}
                  >
                    Balance
                  </th>
                </tr>
              </thead>
              <tbody className="client">
                {clientArr &&
                  clientArr.map((elm, i) => {
                    return (
                      <Client item={elm} key={i} filterValue={this.filters} />
                    );
                  })}
              </tbody>
            </Table>
            {clientArr && clientArr.length > 0 && (
              <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0">
                <Col className="d-flex justify-content-md-end justify-content-sm-start">
                  {totalPage > 1 && (
                    <PaginationBar
                      setting={paginationSetting}
                      handleClick={this.selectPage}
                    />
                  )}
                </Col>

                <Col
                  className="d-flex align-items-baseline mb-md-0 mb-2"
                  xs="auto"
                >
                  {/* <Label className="mr-1 text-nowrap" for="selectPageSize">
                    <small>View</small>
                  </Label> */}
                  <Input
                    onChange={this.selectPageSize}
                    value={limit}
                    bsSize="md"
                    type="select"
                    name="selectPageSize"
                    id="selectPageSize"
                  >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="20">20</option>
                    <option value="50">50</option>
                  </Input>
                  <Label className="ml-1 text-nowrap">
                    {/* <small>
                      {`Showing ${paginationInfo.from} ${
                        paginationInfo.getShowOne()
                          ? ""
                          : `to ${paginationInfo.to}`
                      } of ${paginationInfo.total}`}
                    </small> */}

                    <small>
                      Page {`${selected + 1}`} of {`${totalPage}`}
                    </small>
                  </Label>
                </Col>
              </Row>
            )}
          </Card>
        </Col>
        {/* <Row className="d-flex align-items-center justify-content-center m-3">
          <Link to={`/provider/clients/create`}>
            <Button color="success">Add Client</Button>
          </Link>
        </Row> */}
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    clientData: state.client.list,
    loading: state.client.loading,
  };
};

const mapActionToProps = { getList };

export default connect(mapStateToProps, mapActionToProps)(ListClientAccount);
